:root {
  /* Tipografia Fluida (Mobile -> Desktop) */
  /* Valores exatos do Design System 2.2 */
  --font-display: clamp(2.5rem, 5vw, 4.5rem);
  /* 40px -> 72px */
  --font-h1: clamp(2rem, 4vw, 3.5rem);
  /* 32px -> 56px */
  --font-h2: clamp(1.5rem, 3vw, 2.5rem);
  /* 24px -> 40px */
  --font-h3: clamp(1.25rem, 2vw, 1.75rem);
  /* 20px -> 28px */
  --font-body: clamp(1rem, 1.2vw, 1.125rem);
  /* 16px -> 18px */

  --font-small: 0.875rem;
  /* 14px */
  --font-micro: 0.75rem;
  /* 12px */

  /* Espaamento Global */
  --container-padding: clamp(24px, 5vw, 96px);

  /* Ghost Cards */
  --ghost-card-height-desktop: 480px;
}

body {
  font-family: 'TT Norms Pro', ui-sans-serif, system-ui, sans-serif;
  background-color: #040013;
  /* BRAND.colors.background */
  color: #fcffff;
  /* BRAND.colors.text */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* Mobile-First: Safe area padding for notch/dynamic island */
  padding-left: env(safe-area-inset-left, 0);
  padding-right: env(safe-area-inset-right, 0);
  padding-bottom: env(safe-area-inset-bottom, 0);

  /* Prevent horizontal overflow on mobile */
  overflow-x: hidden;

  /* Optimize text rendering */
  text-rendering: optimizeLegibility;
}

/* Utilitrios de Tipografia baseados nos Tokens */
.text-display {
  font-size: var(--font-display);
  font-weight: 900;
  /* Black */
  line-height: 1.1;
}

.text-h1 {
  font-size: var(--font-h1);
  font-weight: 700;
  /* Bold */
  line-height: 1.1;
}

.text-h2 {
  font-size: var(--font-h2);
  font-weight: 700;
  /* Bold */
  line-height: 1.15;
}

.text-h3 {
  font-size: var(--font-h3);
  font-weight: 500;
  /* Medium */
  line-height: 1.2;
}

.text-body {
  font-size: var(--font-body);
  font-weight: 400;
  /* Regular */
  line-height: 1.5;
}

.text-micro {
  font-family: 'PPSupplyMono', monospace;
  font-size: var(--font-micro);
  line-height: 1.4;
}

/* Card sizing helpers  same height on desktop, 5x4 on mobile */
.card-shell {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  aspect-ratio: 5 / 4;
  min-height: 0;
}

@media (min-width: 768px) {
  .card-shell {
    aspect-ratio: unset;
    min-height: var(--ghost-card-height-desktop);
    height: var(--ghost-card-height-desktop);
  }
}

.card-media {
  position: relative;
  flex: 1 1 auto;
  width: 100%;
  min-height: 0;
}

/* Layout & Grid System (Strict Standard) */
.std-grid {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.5rem;
  /* Mobile: 24px */
  padding-right: 1.5rem;
}

@media (min-width: 768px) {
  .std-grid {
    padding-left: 3rem;
    /* Tablet: 48px */
    padding-right: 3rem;
  }
}

@media (min-width: 1024px) {
  .std-grid {
    padding-left: 6rem;
    /* Desktop: 96px */
    padding-right: 6rem;
    max-width: 1680px;
    /* Max-width constraint */
  }
}

/* Lenis styles from about-origin.css */
html.lenis {
  height: auto;
}

.lenis.lenis-smooth {
  scroll-behavior: auto;
}

.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}

.lenis.lenis-stopped {
  overflow: hidden;
}

/* ========================================================================
   Admin pages: keep native scrolling (no Lenis)
   ======================================================================== */
html.admin-page {
  scroll-behavior: auto !important;
}

html.admin-page,
html.admin-page body {
  overflow-y: auto !important;
  height: auto;
}

.img-wrapper {
  clip-path: inset(0% 0% 0% 0%);
  will-change: clip-path;
}

/* ========================================================================
   Mobile-First Utilities
   ======================================================================== */

/* Touch Target - Minimum 48px for accessibility (WCAG 2.5.5) */
.touch-target {
  min-width: 48px;
  min-height: 48px;
}

/* Safe Area Padding - For notch/dynamic island devices */
.safe-area-bottom {
  padding-bottom: max(16px, env(safe-area-inset-bottom, 16px));
}

.footer-safe-area {
  padding-bottom: env(safe-area-inset-bottom, 0);
}

.safe-area-top {
  padding-top: max(16px, env(safe-area-inset-top, 16px));
}

.safe-area-x {
  padding-left: max(16px, env(safe-area-inset-left, 16px));
  padding-right: max(16px, env(safe-area-inset-right, 16px));
}

/* Touch Feedback - Visual response for mobile tap */
.touch-feedback {
  transition:
    transform 150ms ease,
    opacity 150ms ease;
}

.touch-feedback:active {
  transform: scale(0.97);
  opacity: 0.9;
}

/* Scroll Snap for mobile carousels */
.scroll-snap-x {
  scroll-snap-type: x mandatory;
  overflow-x: auto;
}

/* Hide scrollbar cross-browser */
.scroll-snap-x::-webkit-scrollbar {
  display: none;
}

.scroll-snap-x {
  -ms-overflow-style: none;
}

.scroll-snap-item {
  scroll-snap-align: start;
  scroll-snap-stop: always;
}

/* Mobile-optimized text sizes */
.text-mobile-hero {
  font-size: clamp(2.5rem, 10vw, 6rem);
  line-height: 0.95;
  letter-spacing: -0.03em;
}

.text-mobile-title {
  font-size: clamp(1.5rem, 6vw, 3rem);
  line-height: 1.1;
}

.text-mobile-body {
  font-size: clamp(0.875rem, 3.5vw, 1.125rem);
  line-height: 1.6;
}

